"use client";
import { useEffect, useState } from "react";

const AnyaButlerPage = () => {
  const [isActive, setIsActive] = useState(false);

  useEffect(() => {
    setTimeout(() => {
      setIsActive(true);
    }, 3000);
  }, []);

  return (
    <div className="bg-white p-6 min-h-screen text-center">
      <h1 className="text-3xl font-bold text-gray-800">AURA - 집중력 분석</h1>
      <div
        className={`mt-6 p-6 border-2 border-blue-500 rounded-lg ${isActive ? "animate-pulse" : ""}`}
      >
        {isActive ? "알람: 집중력 저하 - 휴식 필요" : "현재 상태: 피로도 70% 감지"}
      </div>
    </div>
  );
};

export default AnyaButlerPage;